import React, { FC, useState, useEffect } from 'react';
import { InlineLabel, InlineSwitch, LegacyForms } from '@grafana/ui';
import SwitchInput from '../common/SwitchInput';
import CustomInput from '../common/CustomInput';

const { FormField } = LegacyForms;

interface VpcProps {
  [name: string]: any;
}
const QueryVpc: FC<any> = ({ onChange }) => {
  const [vpcquery, setVpcQuery] = useState<VpcProps>({});
  const [filterState, setFilterState] = useState<boolean>(false);
  useEffect(() => {
    onChange && onChange(vpcquery);
  }, [vpcquery, onChange]);
  return (
    <div className="common-content">
      <div className="form-field">
        <FormField
          tooltip=".  NextToken .  5 - 1000"
          label="MaxResults"
          labelWidth={10}
          inputWidth={20}
          placeholder="5~1000"
          onChange={(e) => setVpcQuery((state) => ({ ...state, MaxResults: e.target.value }))}
        />
      </div>
      <div className="form-field">
        <FormField
          tooltip=" token"
          label="NextToken"
          labelWidth={10}
          inputWidth={20}
          onChange={(e) => setVpcQuery((state) => ({ ...state, NextToken: e.target.value }))}
        />
      </div>
      <SwitchInput
        label="ProjectId"
        onChange={(value: any) => setVpcQuery((state) => ({ ...state, ProjectId: value }))}
      />
      <SwitchInput
        tooltip="ID"
        label="VpcPeeringConnectionId"
        onChange={(value: any) => setVpcQuery((state) => ({ ...state, VpcPeeringConnectionId: value }))}
      />
      <SwitchInput
        label="Filter"
        onChange={(value: any) => setVpcQuery((state) => ({ ...state, ['vpc-id']: value }))}
      />
      <div className="gf-form">
        <InlineLabel width={24}>Filter</InlineLabel>
        <InlineSwitch
          onChange={(v: any) => {
            if (!v.target.checked) {
              setVpcQuery((state) => ({ ...state, Filter: {} }));
            }
            setFilterState(v.target.checked);
          }}
        ></InlineSwitch>
      </div>
      {filterState ? (
        <>
          <div className="gf-form">
            <InlineLabel width={24} tooltip="VPCID">
              vpc-id
            </InlineLabel>
            <CustomInput
              onChange={(value: any) => {
                const Filter = vpcquery?.Filter || {};
                Filter['vpc-id'] = value;
                setVpcQuery((state) => ({ ...state, Filter }));
              }}
            />
          </div>
          <div className="gf-form">
            <InlineLabel width={24} tooltip="VPCID">
              accept-vpc-id
            </InlineLabel>
            <CustomInput
              onChange={(value: any) => {
                const Filter = vpcquery?.Filter || {};
                Filter['accept-vpc-id'] = value;
                setVpcQuery((state) => ({ ...state, Filter }));
              }}
            />
          </div>
          <div className="gf-form">
            <InlineLabel
              width={24}
              tooltip="peeringpending-acceptanceactive rejected failed expired provisioning "
            >
              state
            </InlineLabel>
            <CustomInput
              onChange={(value: any) => {
                const Filter = vpcquery?.Filter || {};
                Filter['state'] = value;
                setVpcQuery((state) => ({ ...state, Filter }));
              }}
            />
          </div>
        </>
      ) : null}
    </div>
  );
};

export default QueryVpc;
