import React, { FC, useState, useEffect } from 'react';
import { InlineSwitch, InlineLabel, LegacyForms } from '@grafana/ui';
import SwitchIncrement from '../common/SwtichIncrement';
import CustomIncrement from '../common/CustomIncrement';

const { FormField } = LegacyForms;
const QueryPeering: FC<any> = ({ onChange }) => {
  const [filterState, setFilterState] = useState(false);
  const [peeringquery, setPeerQuery] = useState<{ [name: string]: any }>({});
  useEffect(() => {
    onChange && onChange(peeringquery);
  }, [peeringquery, onChange]);
  return (
    <div className="common-content">
      <div className="form-field">
        <FormField
          tooltip=".  NextToken .  5 - 1000"
          label="MaxResults"
          labelWidth={10}
          inputWidth={20}
          placeholder="5~1000"
          onChange={(e) => setPeerQuery((state) => ({ ...state, MaxResults: e.target.value }))}
        />
      </div>
      <div className="form-field">
        <FormField
          tooltip=" token"
          label="NextToken"
          labelWidth={10}
          inputWidth={20}
          placeholder="NextToken"
          onChange={(e) => setPeerQuery((state) => ({ ...state, NextToken: e.target.value }))}
        />
      </div>
      <SwitchIncrement
        label="ProjectId"
        tooltip="ID"
        datasource={['']}
        onChange={(value) => setPeerQuery((state) => ({ ...state, ProjectId: value }))}
      />
      <SwitchIncrement
        label="VpcPeeringConnectionId"
        tooltip="ID"
        datasource={['']}
        onChange={(value) => setPeerQuery((state) => ({ ...state, VpcPeeringConnectionId: value }))}
      />
      <div className="gf-form">
        <InlineLabel width={24}>Filter</InlineLabel>
        <InlineSwitch
          value={filterState}
          onChange={(v: { target: any }) => {
            setFilterState(v.target.checked);
            if (!v.target.checked) {
              setPeerQuery((state) => ({ ...state, Filter: {} }));
            }
          }}
        />
      </div>
      {filterState ? (
        <>
          <CustomIncrement
            label="vpc-id"
            tooltip="VPCID"
            datasource={['']}
            onValueChange={(value: any) => {
              const Filter = peeringquery?.Filter || {};
              Filter['vpc-id'] = value;
              setPeerQuery((state) => ({ ...state, Filter }));
            }}
          />
          <CustomIncrement
            label="accept-vpc-id"
            tooltip="VPCID"
            datasource={['']}
            onValueChange={(value: any) => {
              const Filter = peeringquery?.Filter || {};
              Filter['accept-vpc-id'] = value;
              setPeerQuery((state) => ({ ...state, Filter }));
            }}
          />
          <CustomIncrement
            label="state"
            tooltip="peeringpending-acceptanceactive rejected failed expired provisioning "
            datasource={['']}
            onValueChange={(value: any) => {
              const Filter = peeringquery?.Filter || {};
              Filter['state'] = value;
              setPeerQuery((state) => ({ ...state, Filter }));
            }}
          />
        </>
      ) : null}
    </div>
  );
};

export default QueryPeering;
