export const kecReducerFn = (state: any, action: any) => {
  const {
    type,
    payload: { key, value },
  } = action;
  switch (type) {
    case 'change':
      return { ...state, [key]: value };
    case 'changeFilter':
      return { ...state, Filter: { ...state.Filter, [key]: value } };
    default:
      return { ...state };
  }
};
