import React, { FC, useState, useEffect } from 'react';
import { LegacyForms } from '@grafana/ui';

const { FormField } = LegacyForms;

const KcsQueryOptions = [
  {
    label: 'AvailableZone',
    key: 'AvailableZone',
    tooltip: 'a',
  },
  {
    label: 'CacheId',
    key: 'CacheId',
    tooltip: 'ID',
  },
  {
    label: 'Name',
    key: 'Name',
    tooltip: '',
  },
  {
    label: 'Vip',
    key: 'Vip',
    tooltip: 'VIP',
  },
  {
    label: 'VpcId',
    key: 'VpcId',
    tooltip: 'ID',
  },
  {
    label: 'VnetId',
    key: 'VnetId',
    tooltip: 'ID',
  },
  {
    label: 'Offset',
    key: 'Offset',
    tooltip: '',
  },
  {
    label: 'Limit',
    key: 'Limit',
    tooltip: '',
    placeholder: '1~100',
  },
  {
    label: 'IamProjectId',
    key: 'IamProjectId',
    tooltip: 'ID,',
  },
  {
    label: 'FuzzySearch',
    key: 'FuzzySearch',
    tooltip: 'IDIP',
  },
  {
    label: 'Protocol',
    key: 'Protocol',
    tooltip: '4.05.0',
  },
  {
    label: 'TagKey',
    key: 'TagKey',
    tooltip: 'key',
    placeholder: '',
  },
  {
    label: 'TagValue',
    key: 'TagValue',
    tooltip: 'value',
    placeholder: '',
  },
];
const QueryKcs: FC<any> = ({ onChange }) => {
  const [kcsquery, setKcs] = useState<{ [stateName: string]: string }>({});
  useEffect(() => {
    onChange && onChange(kcsquery);
  }, [kcsquery, onChange]);

  return (
    <div className="common-content">
      {KcsQueryOptions.map((item: any, index: number) => (
        <div className="repeat-field" key={item.key + index}>
          <FormField
            label={item.label}
            labelWidth={8}
            inputWidth={20}
            placeholder={item?.placeholder || ''}
            onChange={(e) => {
              setKcs((state) => ({ ...state, [item.key]: e.target.value }));
            }}
            tooltip={item.tooltip}
          />
        </div>
      ))}
    </div>
  );
};

export default QueryKcs;
